$color: (
  bg_from: #b1d056,
  bg_end: #eeaa37,
  text_from: #94bd1d,
  text_end: #f98702,
);

.skill_box {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  padding: 15px;
  background-color: #000;
  max-width: 600px;

  @media screen and (max-width: 480px) {
    margin-left: 5px;
    margin-right: 5px;
    width: 100%;
  }

  h3 {
    margin-bottom: 15px;
    color: #fff;
  }
}

.stacks {
  ul {
    li {
      color: #fff;
    }
  }
}

.skills {
  margin-bottom: 15px;

  ul {
    list-style-type: none;
    padding: 0px;
  }

  li {
    padding: 5px;
    margin-bottom: 15px;
    position: relative;
    border-radius: 5px;

    span {
      color: map-get($color, text_from);
      position: relative;

      &.bar {
        background: map-get($color, bg_from);
        background: linear-gradient(
          to right,
          map-get($color, bg_from),
          map-get($color, bg_end)
        );
        position: absolute;
        left: 0px;
        bottom: -7px;
        height: 8px;
        border-radius: 5px;
        box-shadow: 1px 1px 3px rgba(#000, 0.5), 1px 1px 3px rgba(#333, 0.6);
      }

      b {
        color: map-get($color, text_end);
        font-weight: normal;
        position: absolute;
        bottom: 0px;
        right: -20px;
        margin-bottom: 10px;
        font-size: 13px;
        // display: none;
        transition: 1s;
      }
    }

    &:after {
      content: "";
      width: 100%;
      height: 8px;
      background-color: #ddd;
      position: absolute;
      left: 0px;
      bottom: -10px;
      z-index: -1;
      border-radius: 5px;
      margin-bottom: 2px;
      box-shadow: 0px 0px 1px #feb, 2px 2px 8px #999 inset;
    }
  }
}
